import React from 'react'
import Checker from '../Checker'
import Arrow from './arrow2.svg'
import Logo from './logo.png'

const style = {
	root: {
		width: '90vw',
		maxWidth: '300px',
		minHeight: '200px',
		backgroundColor: '#ff8201',
		borderRadius: '10px',
	},
	body: {
		margin: '20px 0',
	},
	header: {
		textAlign: 'center',
		color: '#333333',
		padding: '3px',
		fontSize: '12px',
    	fontWeight: '600',
	},
	element: index => ({
		color: '#333333',
		padding: '5px',
		fontSize: '12px',
    	fontWeight: '600',
		backgroundColor: index % 2 ? '#ece9c7' : '#fffed2'
	})
}

const months = ["Ja", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
const days = ["Sun", "Mon", "Tue", "Wednesday", "Thu", "Fri", "Sat"];

const getHeaderText = () => {
	const date = new Date()
	const day = days[date.getDay()]
	const day1 = date.getDate()
	const month = months[date.getMonth()]
	const year = date.getFullYear()
	return `Activated: ${day} ${day1} ${month} ${year} 07:31`
}

const getShorterDate = () => {
	const date = new Date()
	const day1 = date.getDate()
	const month = months[date.getMonth()]
	const year = date.getFullYear()
	return `${day1} ${month} ${year}`
}

class Card extends React.Component { 
	
	state = {
		flip: false
	}

	render() {
		return(
			<div style={style.root}>
				<div style={style.header}>{getHeaderText()}</div>
				<section>
					<div style={style.element(0)}><Checker/></div>
					<div style={style.element(1)}>Anytime Day Return (Outward)</div>
					<div style={style.element(2)}>Adult Standard Class</div>
					<div style={style.element(3)}>
						<div style={{marginBottom: '10px'}}>{this.state.flip ? 'Bolton' : 'Buckshaw Parkaway'}</div>
						<section style={{display: 'flex'}}>
							<img src={Arrow}/>
							<div style={{marginLeft: '10px'}}>{this.state.flip ? 'Buckshaw Parkaway' : 'Bolton'}</div>
						</section>
					</div>
					<div style={style.element(4)}>Valid via any permitted route</div>
					<div style={style.element(5)}>Travel on:<br/>{getShorterDate()}</div>
					<div style={style.element(6)}>Price: £8.30</div>
				</section>
				<img onClick={()=>this.setState(state=>({flip: !state.flip}))} src={Logo} style={{
					display: 'flex',
					height: '12px',
					margin: '3px 0 3px 20px'
				}}/>
			</div>
		)
	}
}

export default Card


